<template>
    <v-container fluid grid-list-md style="margin: 0px;" id="mycontainer">
        <v-layout row wrap style="height: 100%;" @keydown.left="minusCardIndex()" @keydown.right="addCardIndex()">
            
            <v-flex d-flex md4 sm3 xs2>
                <v-layout column justify-center>
                    <div class="text-xs-center">
                        <v-btn color="error" fab dark @click="minusCardIndex()" id="prevButton">
                            <v-icon> arrow_left </v-icon>
                        </v-btn>
                    </div>
                </v-layout>
            </v-flex>
            
            <v-flex d-flex md4 sm6 xs8>
                <v-layout column justify-center>
                    <v-card class="mx-auto" color="#26c6da" dark max-width="400" @keydown.left="minusCardIndex()" @keydown.right="addCardIndex()">
                        <v-card-title>
                            <v-icon style="margin-right: 5px;">
                                event
                            </v-icon>
                            <span class="title" style="font-weight: bold">{{ currentCard['date'] }}</span>
                        </v-card-title>

                        <v-card-text class="headline font-weight-bold" style="white-space: pre-line;">{{ currentCard['content'] }}</v-card-text>
                        
                        <v-card-actions>
                            <v-list-tile class="grow">
                                <v-list-tile-avatar color="grey darken-3">
                                    <v-img class="elevation-6" src="https://photos.dcard.tw/memberPhotos/4b6e01e7-8506-4058-b9d6-07bedeb933ec">
                                    </v-img>
                                </v-list-tile-avatar>

                                <v-list-tile-content>
                                    <v-list-tile-title>
                                        <strong></strong>
                                    </v-list-tile-title>
                                </v-list-tile-content>

                                <v-layout align-center justify-end>
                                    <v-icon class="mr-1" style="color: #c16666;">favorite</v-icon>
                                    <span class="subheading mr-2 heartText">309</span>
                                    <span class="mr-1"> </span>
                                    <v-icon class="mr-1" style="color: #2f6aef;">message</v-icon>
                                    <span class="subheading msgText">106</span>
                                </v-layout>
                            </v-list-tile>
                        </v-card-actions>
                    </v-card>
                </v-layout>
            </v-flex>

            <v-flex d-flex md4 sm3 xs2>
                <v-layout column justify-center>
                    <div class="text-xs-center">
                        <v-btn color="success" fab dark @click="addCardIndex()">
                            <v-icon> arrow_right </v-icon>
                        </v-btn>
                    </div>
                </v-layout>
            </v-flex>
            
        </v-layout>
    </v-container>
</template>

<script>
  import MessagesData from '../mock_server/data.json';

  export default {
    name: 'main',
    data: function() {
        return {
            mycards: [],
            currentCard: {},
            cardIndex: 0
        }
    },
    
    watch: {
        cardIndex: function() {
            this.currentCard = this.mycards[this.cardIndex];
        }
    },

    created: function() {
        this.mycards = MessagesData;
    },

    mounted() {
        this.currentCard = this.mycards[this.cardIndex];

        document.getElementById("prevButton").focus();
    },

    updated: function () {

    },

    methods: {
        addCardIndex: function() {
            if(this.cardIndex < this.mycards.length - 1)
                this.cardIndex += 1;
        },

        minusCardIndex: function() {
            if(this.cardIndex > 0)
                this.cardIndex -= 1; 
        }
    }
  }
</script>

<style scoped>
    .heartText {
        color: #c16666;
        font-weight: bold;
    }

    .msgText {
        color: #2f6aef;
        font-weight: bold;
    }
</style>